/*
  Bootstrap 4 12 Column flex grid - set the variables in variables.scss
  Use .container or .container-fluid to wrap .row and .col-xx
*/

@-ms-viewport {
    width: device-width;
}

html {
    box-sizing: border-box;
    -ms-overflow-style: scrollbar;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

.container {
    margin-right: auto;
    margin-left: auto;
    padding-right: 28px;
    padding-left: 28px;
    width: 100%;
} // Medium
@media (min-width: $medium-breakpoint) {
    .container {
        max-width: $medium-container-max-width;
        padding-left: $gutter-size-medium;
        padding-right: $gutter-size-medium;
    }
} // Large
@media (min-width: $large-breakpoint) {
    .container {
        max-width: $large-container-max-width;
        padding-left: $gutter-size-large;
        padding-right: $gutter-size-large;
    }
} // Extra Large
@media (min-width: $extra-large-breakpoint) {
    .container {
        max-width: $extra-large-container-max-width;
        padding-left: $gutter-size-large;
        padding-right: $gutter-size-large;
    }
}

.container-fluid {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-left: $gutter-size-small;
    padding-right: $gutter-size-small;
}

@media (min-width: $medium-breakpoint) {
    .container-fluid {
        padding-left: $gutter-size-medium;
        padding-right: $gutter-size-medium;
    }
}

@media (min-width: $large-breakpoint) {
    .container-fluid {
        padding-left: $gutter-size-large;
        padding-right: $gutter-size-large;
    }
}

@media (min-width: $extra-large-breakpoint) {
    .container-fluid {
        padding-left: $gutter-size-large;
        padding-right: $gutter-size-large;
    }
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -$gutter-size-small;
    margin-left: -$gutter-size-small;
}

@media (min-width: $medium-breakpoint) {
    .row {
        margin-left: -$gutter-size-medium;
        margin-right: -$gutter-size-medium;
    }
}

@media (min-width: $large-breakpoint) {
    .row {
        margin-left: -$gutter-size-large;
        margin-right: -$gutter-size-large;
